:root {
    /*primary background, text blue */
    --clr-primary-grey: #252832;
    /*primary background, text blue */
    --clr-secondary-grey: #f5f4f4;
  
    /*secondary footer blue */
    --clr-primary-blue: #33acd1;
  
   
    /*primary button colour */
    --clr-button-primary: #33d188;
    /*primary text colour*/
    --clr-text-primary: #252832;
    /*secondary text colour*/
    --clr-text-secondary: #fff;
    /* tertiary text coloyr*/
    --clr-text-tertiary:#70737C;
    
    --transition: all 0.1s linear;
    --spacing: 0.1rem;
    --radius: 0.5rem;
    --light-shadow: 0 5px 15px rgba(0, 0, 0, 0.1);
    --dark-shadow: 0 5px 15px rgba(0, 0, 0, 0.2);
    --max-width: 1170px;
    --fixed-width: 620px;
  }

  .sidebar-container{
      height: 100vh;
      width: 15vw;
      background-color: var(--clr-text-secondary) ;
      font-family: 'Open Sans', sans-serif;
      color: var(--clr-primary-grey);
  }

  .sidebar-container img {
      padding-top: 4rem;
      display: block;
    margin: 0 auto;
      width: 80%;
  }

  ul{
    margin: 0 auto;
    padding: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
     
  }

  .sidebar-links li{

   height: 10vh;
    
    list-style-type: none;
    width: 100%;
  }

  .sidebar-links li:hover{
      cursor: pointer;
background-color: var(--clr-primary-blue);
color: var(--clr-text-secondary);
  }
  .sidebar-link{
    padding-left: 4rem;
    display: flex;
  align-items: center;
  text-decoration: none;
  height: 100%;
      color: var(--clr-primary-grey);
      font-size: 0.8rem;
      font-weight: bold;
  
  }

  .sidebar-link:hover{
      color: var(--clr-text-secondary);
  }

